<template>
  <section>
    <p class="show-msg">{{ displayMsg }}</p>
    <div class="pomoClock">
      <p>{{ pomoMinute(this.$store.state.displayTime) }}Ôºö{{ pomoSecond(this.$store.state.displayTime) }}</p>
      <span v-if="!isBreak">
        <i
          class="fa fa-pause-circle"
          aria-hidden="true"
          v-if="isCount"
          @click="pauser"
        ></i>
        <i
          class="fa fa-play-circle"
          aria-hidden="true"
          v-else
          @click="pomoer"
        ></i>
      </span>
      <i
        class="fa fa-stop-circle"
        aria-hidden="true"
        v-if="!isInit"
        @click="stopper"
      ></i>
    </div>
  </section>
</template>

<script>
export default {
  props:[],
  data() {
    return {
      displayMsg: null,
      // displayTime: null,
      countInterval: null,
      notifyGranted: false,
      isBreak: false,
      isInit: true,
      isCount: false,
      msg: {
        beforePomo: "ÈñãÂßãÁÜ±Ë°ÄüçÖÂêß",
        pause: "üçÖÂú®Á≠â‰Ω†Êé°Êî∂Âñî~",
        pomo: "\\ Áï™ËåÑüçÖÊôÇÂÖâ /",
        shortBreak: "Â∞èÊ≠áÁâáÂàªÔºåüçÖÁÜü‰∫ÜÊúÄÂ•ΩÂêÉ",
        longBreak: "Â•ΩÂ•Ω‰ºëÊÅØÔºåÁ≠â‰∏ã‰∏ÄÊ£µÁï™ËåÑÊ®π‚Ä¶‚Ä¶",
      }
    };
  },
  beforeMount() {
    this.initPomo();
    this.initNotify();
  },
  methods: {
    initPomo() {
      this.isBreak = false;
      this.isCount = false;
      this.isInit = true;
      this.displayMsg = this.msg.beforePomo;
      this.$store.commit('initDisplayTime')
    },
    initNotify() {
      if (!("Notification" in window)) {
        console.log("This browser does not support notification");
      } else {
        console.log(Notification.permission);
      }
      switch (Notification.permission) {
        case "default":
        case "denied":
        case "undefined":
          Notification.requestPermission();
          break;
        case "granted":
          this.notifyGranted = true;
          break;
      }
    },
    //on-click
    pomoer() {
      this.isCount ? this.pause2recount() : this.start2countdown();
    },
    pauser() {
      this.countdown2pause();
    },
    stopper() {
      this.countdown2stop();
    },
    //ÂÄíÊï∏Âô®
    countdown() {
      this.timeInterval = setInterval(() => {
        this.$store.state.displayTime = this.$store.state.displayTime - 1;
        if (this.$store.state.displayTime < 0) {
          this.notifyGranted ? this.showNotify() : this.showAlert();
          clearInterval(this.timeInterval);
          this.$store.state.displayTime = 0;
          this.check_break_start();
        }
      }, 1000);
    },
    check_break_start() {
      this.isBreak ? this.break2start() : this.countdown2break();
    },
    // ------------ÁãÄÊÖãÊ©ü-----------
    start2countdown() {
      this.isInit = false;
      this.isCount = true;
      this.displayMsg = this.msg.pomo;
      this.countdown();
    },
    //Êö´ÂÅú
    countdown2pause() {
      this.isCount = false;
      this.displayMsg = this.msg.pause;
      clearInterval(this.timeInterval);
    },
    pause2recount() {
      this.displayMsg = this.msg.pomo;
      this.countdown();
    },
    //ÂÄíÊï∏~‰ºëÊÅØ
    countdown2break() {
      this.isBreak = true;
      if (this.$parent.timesCookie % 4 != 3) this.setShortBreak();
      else this.setLongBreak();
      this.countdown();
    },
    setShortBreak() {
      this.displayMsg = this.msg.shortBreak;
      this.$store.state.displayTime = this.$store.state.time.shortBreak;
    },
    setLongBreak() {
      this.displayMsg = this.msg.longBreak;
      this.$store.state.displayTime = this.$store.state.time.longBreak;
    },
    break2start() {
      this.$parent.timesCookie++;
      this.initPomo();
    },
    countdown2stop() {
      this.ask("Ëá®ÊôÇÂÅúÊ≠¢ÁöÑË©±Â∞±ÁÑ°Ê≥ïÊé°Êî∂ÈÄôÈ°ÜüçÖ‰∫ÜÔºÅ\nÁ¢∫ÂÆöË¶ÅÂÅúÊ≠¢ÂóéÔºü", () => {
        clearInterval(this.timeInterval);
        this.initPomo();
      });
    },
    ask(question, yes) {
      if (confirm(question)) yes();
    },
    //ÊôÇÈñìÈ°ØÁ§∫
    padZero(numVar, numLength) {
      return (Array(numLength).join("0").slice(-numLength) + numVar).slice(
        -numLength
      );
    },
    pomoMinute(time) {
      return this.padZero(Math.floor(time / 60), 2);
    },
    pomoSecond(time) {
      return this.padZero(time % 60, 2);
    },
    //Êé®Êí≠
    showNotify() {
      this.notify = new Notification("ÊôÇÈñìÂà∞", {
        body: "Á∂†Ëâ≤Áï™ËåÑÈóúÂøÉÊÇ®",
        icon: "../../assets/pomonoti.ico",
      });
    },
    showAlert() {
      alert("ÊôÇÈñìÂà∞");
    },
  },
};
</script>

<style>
section {
  position: absolute;
  font-size: var(--main-font-size);
  top: calc(42px + 50vh - (var(--main-font-size) + 1em * 2) + 30px);
  /* Ë¶ñË¶∫‰∏äÁΩÆ‰∏≠ÔºöheaderÈ´ò+Ëû¢Âπï‰∏ÄÂçäÈ´ò-(msgÂ≠óÁ∏ΩÈ´ò+msg margin) + footerÈ´ò) */
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: -2;
}

section .pomoClock {
  text-align: center;
  background-color: #fff;
}

.fa {
  color: var(--green);
  font-size: calc(var(--main-font-size) + 1.5rem);
  cursor: pointer;
  margin: 0 10px;
}
</style>
